import { Component, Input, OnInit, Output, SimpleChanges, EventEmitter } from '@angular/core';
import { VhQuerySales } from 'ionic-vhframeworks';

@Component({
  selector: 'app-fb-info-user',
  templateUrl: './fb-info-user.component.html',
  styleUrls: ['./fb-info-user.component.scss']
})
export class FbInfoUserComponent implements OnInit {
  @Input() infoSender: any = {}
  @Output() addCustomerEvent = new EventEmitter<any>()
  showDrawerAddCusomer: boolean = false
  visibleInfoCustomer: boolean = false
  constructor(
    private vhQuerySales: VhQuerySales
  ) { }

  ngOnInit(): void {

  }
  ngOnChanges(changes: SimpleChanges) {
    if (changes.infoSender) {
      this.infoSender = changes.infoSender.currentValue
       this.getLocalCustomer()
    }
  }
  getLocalCustomer() {
    let person: any = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).find(item => {
      if (item.email) {
        let email = this.infoSender.id + '@facebook.com';
        if (email == item.email) return true
      }
    })
    person && (this.infoSender = { ...this.infoSender, ...person })
  }
  closeAddCustomer(result) {
    this.showDrawerAddCusomer = false;
    if(result){
      this.addCustomerEvent.emit(result)
      
      this.infoSender = {...result,...this.infoSender}
    }
  }
  closeInfoCustomer(event) {
    this.visibleInfoCustomer = false
  }
}
