import { DatePipe } from '@angular/common';
import { Component, Input, OnInit, SimpleChanges } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { VhAlgorithm } from 'ionic-vhframeworks';
import { filter } from 'rxjs/operators';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FacebookService } from '../../../services/facebook.service';

@Component({
  selector: 'app-conversation-dialog',
  templateUrl: './conversation-dialog.component.html',
  styleUrls: ['./conversation-dialog.component.scss']
})
export class ConversationDialogComponent implements OnInit {
  conversationId: string = ''
  loading: boolean = false
  @Input() fanpageSelected: any = {}
  @Input() infoSender: any = {}
  @Input() pushMessage: any = {}
  messageList: Array<any> = []
  dataPaging: any = {}
  messageText: string = ''
  loadingMoreMessage: boolean = false
  listAttachment: Array<any> = []
  loadingAttachment: boolean = false
  searchProductList: boolean = false
  productSelected: any = {}
  expiresToSend: boolean = false
  constructor(
    private fbService: FacebookService,
    private vhComponent: VhComponent,
    private router: Router,
    private routes: ActivatedRoute,
    public vhAlgorithm: VhAlgorithm,
    private datePiPe: DatePipe
  ) { }

  ngOnInit(): void {
    this.subscribeRouter()
  }
  //subscribe router
  subscribeRouter() {
    this.routes.queryParams.pipe(filter(params => params.conversationId))
      .subscribe(val => {
        this.conversationId = val.conversationId
        this.conversationId != '' && this.getData()
      }
      );
  }
  ngOnChanges({ pushMessage }: SimpleChanges) {
    if (pushMessage) {
      this.messageList.push(pushMessage.currentValue)
      setTimeout(() => {
        this.scrollToBottom()
      }, 300)
    }
  }
  /**
   * Ly tin nhn v ly thng tin ngi gi
   */
  getData() {
    this.loading = true
    this.fbService.getListMessageByConversationID(this.fanpageSelected['access_token'], this.conversationId).then((res: any) => {
      if (res) {
        let messages: Array<any> = []
        this.dataPaging = res.messages.paging
        res.messages.data.forEach(item => messages.push(this.fbService.getMessageDetailByMessageID(this.fanpageSelected['access_token'], item.id)))
        Promise.all(messages).then((result: Array<any>) => {
          this.messageList = result.map((item, index) => {
            return { ...res.messages.data[index], ...item }
          }).reverse()
          this.getStickerMessage(this.messageList).then((resultSticker: Array<any>) => {
            resultSticker.forEach(item => {
              let index = this.messageList.findIndex((mess: any) => {
                if (mess.shares && mess.shares.data && mess.shares.data[0] && mess.shares.data[0].id == item.data[0].id) return true
                else return false
              })
              if (index >= 0) this.messageList[index].shares = item.data[0]
            })
            this.getLastMessageFromUserAndCheckTime(this.messageList)
            this.loading = false
            setTimeout(() => {
              this.scrollToBottom()
            }, 300)
          }).catch(() => { this.showMessageError(); this.loading = false })

        }).catch(() => { this.showMessageError(); this.loading = false })
      }
    }).catch(() => { this.showMessageError(); this.loading = false })
  }
  /**
   * Hm ny  tm ra tin nhn ln cui khch hng  nhn
   * em so snh vi hin ti, nu > 3 ngy th k th tr li tin nhn c na
   * @param messageList 
   */
  getLastMessageFromUserAndCheckTime(messageList: Array<any>) {
    let lastMessage: any = messageList[messageList.length - 1]
    this.expiresToSend = false
    //3 ngy i ra milisecond = 24*60*60*1000*3 = 259200000
    if (lastMessage.id) {
      if ((Date.now() - 259200000) >= new Date(lastMessage.created_time).getTime()) this.expiresToSend = true
    }
    else this.expiresToSend = true
  }
  /**
   * Ly message sticker
   * @param messageList 
   * @returns 
   */
  getStickerMessage(messageList: Array<any>): Promise<any> {
    return new Promise((resolve, reject) => {
      let shares: Array<any> = []
      messageList.forEach(item => {
        if (item.shares) shares.push(this.fbService.getLinkShares(this.fanpageSelected['access_token'], item.id))
      })
      Promise.all(shares).then((res: any) => { resolve(res) }).catch(() => reject())
    })
  }

  ngAfterViewChecked() {
    if (document.getElementById('conversation-dialog') && document.getElementById('conversation-dialog-message')) {
      let height = document.getElementById('conversation-dialog').clientHeight - (this.listAttachment.length > 0 ? 220 : 160)
      document.getElementById('conversation-dialog-message').style.height = `${height}px`
    }
  }
  public renderDate(date) {
    return this.datePiPe.transform(date, 'dd/MM/YYYY HH:mm')
  }
  trackByFn(index, item) {
    return index;
  }
  /**
   * T ng scroll xung di cng
   */
  scrollToBottom() {
    let element = document.getElementById('conversation-dialog-message')
    element.scrollTop = element.scrollHeight
  }
  /**
   * S kin typing bt k phm no, bt s kin enter  send message
   * @param event 
   */
  keyPress(event: KeyboardEvent) {
    const keyCode = event.which || event.keyCode;
    if (keyCode == 13 && !event.shiftKey) {
      event.preventDefault()
      this.sendAllMessage()
    }
  }
  sendAllMessage() {
    if (this.messageText) {
      let body = {
        recipient: {
          id: this.infoSender['id']
        },
        message: {
          text: this.messageText
        },
        tag: "ACCOUNT_UPDATE",
        messaging_type: "MESSAGE_TAG",
      }
      this.messageList.push({
        id: '',
        from: this.fanpageSelected,
        to: { data: [this.infoSender] },
        message: this.messageText,
        created_time: new Date().toISOString(),
      })
      this.messageText = ''
      setTimeout(() => {
        this.scrollToBottom()
      }, 100)
      this.fbService.sendMessageToUserTypeText(this.fanpageSelected['access_token'], body).then(res => {
        if (res.error) {
          this.vhComponent.alertMessageDesktop('error', res.error?.message);
          this.messageList.splice(this.messageList.length - 1, 1)
        }
        else this.messageList[this.messageList.length - 1].id = res.message_id
        if (this.listAttachment.length > 0) this.sendMessageImage()
      }).catch(err => console.error(err))
    } else if (!this.messageText && this.listAttachment.length > 0) this.sendMessageImage()
  }
  /**
   * Gi hnh nh
   */
  sendMessageImage() {
    this.listAttachment.forEach(item => {
      this.messageList.push({
        id: '',
        from: this.fanpageSelected,
        to: { data: [this.infoSender] },
        message: '',
        created_time: new Date().toISOString(),
        attachments: {
          data: [{ image_data: { preview_url: item.buffer, url: item.buffer } }]
        }
      })
    })
    let listAttachment = [...this.listAttachment]
    this.listAttachment = []
    let processs: Array<any> = []
    for (let item of listAttachment)
      processs.push(this.fbService.sendImageFromFile(this.fanpageSelected['access_token'], this.infoSender['id'], item.attachment_id))
    Promise.all(processs).then((res: Array<any>) => {
      let messages: Array<any> = []
      res.forEach(item => messages.push(this.fbService.getMessageDetailByMessageID(this.fanpageSelected['access_token'], item.message_id)))
      Promise.all(messages).then((result: Array<any>) => {
        let length = this.messageList.length
        result.forEach((mess: any, index: number) => {
          this.messageList[length - result.length + index] = mess
        })
      }).catch(() => { this.showMessageError(); })
    }).catch(() => this.vhComponent.alertMessageDesktop('error', "Xy ra khi gi hnh nh, vui lng th li"))
  }

  appendEmoji(emoji: string) {
    this.messageText += emoji
  }
  loadMoreMessage() {
    this.loadingMoreMessage = true;
    this.fbService.getNextPaging(this.dataPaging.next).then(res => {
      if (res.error) {
        this.showMessageError()
        this.loadingMoreMessage = false
      } else {
        this.dataPaging = res.paging
        let messages: Array<any> = []
        res.data.forEach(item => messages.push(this.fbService.getMessageDetailByMessageID(this.fanpageSelected['access_token'], item.id)))
        Promise.all(messages).then((result: Array<any>) => {
          let message = result.map((item, index) => {
            return { ...res.data[index], ...item }
          }).reverse()
          this.getStickerMessage(message).then((resultSticker: Array<any>) => {
            resultSticker.forEach(item => {
              let index = message.findIndex((mess: any) => {
                if (mess.shares && mess.shares.data && mess.shares.data[0] && mess.shares.data[0].id == item.data[0].id) return true
                else return false
              })
              if (index >= 0) message[index].shares = item.data[0]
            })
            this.messageList = message.concat(this.messageList)
            this.loadingMoreMessage = false
          }).catch(() => { this.showMessageError(); this.loading = false })
          // this.messageList = mess.concat(this.messageList)
          // this.loadingMoreMessage = false
        }).catch(() => { this.showMessageError(); this.loadingMoreMessage = false })
      }
    }).catch(() => { this.showMessageError(); this.loadingMoreMessage = false })
  }
  /**
   * Upload hnh nh
   * @param event 
   */
  onUploadImage(event: any) {
    let file = event.target.files[0]
    let fileBase64 = event.target.files[0]
    if (fileBase64) {
      let readerBase64 = new FileReader();
      readerBase64.readAsDataURL(fileBase64)
      readerBase64.onload = (event: any) => {
        this.listAttachment.push({ attachment_id: '', buffer: readerBase64.result, loadingAttachment: true })
        let reader = new FileReader();
        reader.readAsArrayBuffer(file);
        reader.onload = async (e: any) => {
          let arrayBuffer = e.target.result;
          let blob = new Blob([arrayBuffer], { type: file.type });
          let data = new FormData();
          data.append('message', JSON.stringify({ "attachment": { "type": "image", "payload": { "is_reusable": true } } }));
          data.append('filedata', blob);
          this.fbService.updateImageToSendMessageAndReceiveAttachment(this.fanpageSelected['access_token'], data).then(res => {
            if (res.error) {
              this.listAttachment.splice(this.listAttachment.length - 1, 1)
              this.vhComponent.alertMessageDesktop('error', res.error.message);
            } else {
              this.listAttachment[this.listAttachment.length - 1].attachment_id = res.attachment_id
              this.listAttachment[this.listAttachment.length - 1].loadingAttachment = false
            }
          })
        };
      }
    }
  }
  updateImage() {
    if (this.listAttachment.length > 0 && this.listAttachment[this.listAttachment.length - 1].loadingAttachment == true)
      this.vhComponent.alertMessageDesktop('warning', 'ang ti nh ln, vui lng ch trong giy lt  ti nh tip theo')
    else
      document.getElementById('image-message').click()
  }

  removeImage(image: any, index: number) {
    this.listAttachment.splice(index, 1)
  }
  showMessageError() {
    this.vhComponent.alertMessageDesktop('error', "Xy ra li khi ng b, vui lng th li")
  }
  isVisibleSendMessage: boolean = false
  closeSearch(data) {
    if (data) {
      this.productSelected = data
      this.isVisibleSendMessage = true
    }
    this.searchProductList = false;
  }
  sendProductAndPrice() {
    this.messageText = `Tn sn phm: ${this.productSelected['name']}, Gi : ${this.vhAlgorithm.vhcurrencyunit_symbol(this.productSelected['price'])}`
    this.isVisibleSendMessage = false
  }
  sendMessageUrl(all?) {
    this.messageList.push({
      id: '',
      from: this.fanpageSelected,
      to: { data: [this.infoSender] },
      message: '',
      created_time: new Date().toISOString(),
      attachments: {
        data: [{ image_data: { preview_url: this.productSelected['img'], url: this.productSelected['img'] } }]
      }
    })
    this.isVisibleSendMessage = false
    setTimeout(() => {
      this.scrollToBottom()
    }, 100)
    this.fbService.sendImageWithUrl(this.fanpageSelected['access_token'], this.productSelected['img'], this.infoSender['id']).then(res => {
      if (res.error) {
        this.vhComponent.alertMessageDesktop('error', "Xy ra khi gi hnh nh, vui lng th li")
        this.messageList.splice(this.messageList.length - 1, 1)
      } else this.messageList[this.messageList.length - 1].id = res['message_id']

      if (all) {
        this.sendProductAndPrice()
        this.sendAllMessage()
      }
    })
  }
  gotoInbox() {
    window.open(`https://www.facebook.com/${this.fanpageSelected.id}/inbox`, '_blank')
  }
}
