import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { VhAlgorithm } from 'ionic-vhframeworks';
import { filter } from 'rxjs/operators';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FacebookService } from '../../../services/facebook.service';

@Component({
  selector: 'app-conversation-inbox',
  templateUrl: './conversation-inbox.component.html',
  styleUrls: ['./conversation-inbox.component.scss']
})
export class ConversationInboxComponent implements OnInit {
  @Input() listFanpage: Array<any> = [];
  @Input() newMessage: any = {}
  @Input() newComment: any = {}
  @Output() changeFanpageSelected = new EventEmitter<any>()
  @Output() changeInfoSender = new EventEmitter<any>()
  @Output() emitNewMessageFB = new EventEmitter<any>()
  fanpageSelected: any = {}
  listSubFanpage: Array<any> = [];
  listConversation: Array<any> = []
  listSubConversation: any = []
  listCommentPost: any = []
  listSubCommentPost: any = []
  listUserComment: Array<any> = []
  loading: boolean = false
  conversationId = ''
  commentId = ''
  dataPagingMess: any = {}
  dataPagingPost: any = {}
  searchValue: string = ''
  selectedIndex: number = 0
  loadingMoreMessage: boolean = false
  constructor(
    private fbService: FacebookService,
    private vhComponent: VhComponent,
    private router: Router,
    private routes: ActivatedRoute,
    private vhAlgorithm: VhAlgorithm,
  ) {

  }

  ngOnInit(): void {
    this.fanpageSelected = this.listFanpage[0]
    this.listSubFanpage = [...this.listFanpage]
    this.clearPageSelected()
    this.getData()
    this.subscribeRouter()
  }
  ngOnChanges(changes: SimpleChanges) {
    if (changes.newMessage?.currentValue[0]) {
      let data = changes.newMessage.currentValue[0]
      //tm index xem c trong list conversation cha
      let index = this.listConversation.findIndex(item => item.participants?.data[0]?.id == data.sender?.id)
      if (index >= 0) {
        let conversation = { ...this.listConversation[index] }
        this.listConversation.splice(index, 1)
        conversation.unread_count++
        conversation.messages?.data?.unshift({ ...data.message, webhook_send: true, message: data.message.text || '', created_time: new Date().toISOString() })
        this.listConversation.unshift({ ...conversation, can_reply: true })
        this.listSubConversation = [...this.listConversation]
        //bn message qua chi tit tin nhn
        if (this.listConversation[0].id == this.routes.snapshot.queryParamMap.get('conversationId'))
          this.emitNewMessage(changes.newMessage.currentValue[0])
      } else this.getNewConversation(changes.newMessage.currentValue[0])
    }
    if (changes.newComment?.currentValue?.field == 'feed') {
      let newCommentFB = changes.newComment.currentValue.value
      //Trng hp l comment cha, khng phi reply comment
      if (newCommentFB.post_id == newCommentFB.parent_id) {
        let user = this.listUserComment.find(item => item.id == newCommentFB.from?.id)
        if (user) {
          this.listCommentPost.unshift(this.renderDataNewComment(newCommentFB, user))
          this.listSubCommentPost = [...this.listCommentPost]
        } else {
          this.fbService.getInfoUser(newCommentFB.from.id, this.fanpageSelected['access_token']).then(res => {
            if (!res.error) {
              this.listUserComment.push(res)
              this.renderDataNewComment(newCommentFB, res)
            }
          })
        }
      }
    }
  }
  //bn message qua chi tit tin nhn
  emitNewMessage(messageInfo: any) {
    let data: any = {
      id: messageInfo.message.mid,
      from: { id: messageInfo.sender?.id },
      created_time: new Date().toISOString(),
      message: messageInfo.message.text,
      to: { data: [{ id: messageInfo?.recipient.id }] }
    }
    if (messageInfo.message?.attachments)
      data = {
        ...data,
        attachments: {
          data: [{ id: '', type: messageInfo.message?.attachments[0]?.type, image_data: { url: messageInfo.message?.attachments[0]?.payload?.url, preview_url: messageInfo.message?.attachments[0]?.payload?.url } }]
        }
      }
    this.emitNewMessageFB.emit(data)
  }
  //subscribe router
  subscribeRouter() {
    this.routes.queryParams.pipe(filter(params => (params.conversationId || params.commentId || params.deleteCommentID)))
      .subscribe(val => {
        if (val.conversationId) this.conversationId = val.conversationId
        else if (val.commentId) this.commentId = val.commentId
        else if (val.deleteCommentID) this.deleteCommentInList(val.deleteCommentID)
      }
      );
  }
  deleteCommentInList(id: string) {
    this.listCommentPost.splice(this.listCommentPost.findIndex(item => item.id == id), 1)
    this.changeInfoSender.emit({ id: '' })
    this.router.navigate(['/sales/dashboard/manage/socials-channel/facebook/conversation'])
  }
  ngAfterViewChecked() {
    if (document.getElementById('conversation-inbox') && document.querySelector('.facebook-tabs') && document.querySelector('.facebook-header-user') && document.getElementById('block-conversation-inbox')) {
      let height = document.getElementById('conversation-inbox').clientHeight - document.querySelector('.facebook-tabs').clientHeight
        - document.querySelector('.facebook-header-user').clientHeight - 32
      document.getElementById('block-conversation-inbox').style.height = `${height}px`
    }
  }
  getData() {
    this.loading = true
    //bc u tin l ly danh sch cc cuc hi thoi
    this.fbService.getListConversationID(this.fanpageSelected['access_token'], this.fanpageSelected['id']).then(res => {
      this.listConversation = res.data;
      this.dataPagingMess = res.paging
      let conversation: Array<any> = []
      this.listConversation.forEach(item => {
        //bc 2 i vi mi cuc hi thoi phi get danh sch nhn tin
        conversation.push(this.fbService.getListMessageByConversationID(this.fanpageSelected['access_token'], item.id))
      })
      Promise.all(conversation).then((result: Array<any>) => {
        this.listConversation = result
        // //bc 3 i vi mi tn phi ly ra tn u tin  hin th v get thng tin ngi gi
        let messages: Array<any> = []
        this.listConversation.forEach((item: any) => {
          messages.push(this.fbService.getInfoUserByPSID(item.participants.data[0].id, this.fanpageSelected['access_token']))
        })
        Promise.all(messages).then((results: any) => {
          
          for (let index in results) {
            if (!results[index].error)
              this.listConversation[index].participants.data[0].profile_pic = results[index].profile_pic
            else this.listConversation[index].participants.data[0].profile_pic = 'assets/icon/no-avatar.svg'
            this.listConversation[index].senderName = this.listConversation[index].participants.data[0].name
          }
          let firstMessage: Array<any> = []
          this.listConversation.forEach((item: any) => {
            firstMessage.push(this.fbService.getMessageDetailByMessageID(this.fanpageSelected['access_token'], item.messages?.data[0]?.id))
          })
          this.listSubConversation = this.listConversation
          Promise.all(firstMessage).then((results2: any) => {
            for (let index in results2) {
              if (!results2[index].error)
                this.listConversation[index].messages.data[0] = { ...this.listConversation[index].messages.data[0], ...results2[index] }
            }
            //Bc 4 : ly danh sch cc bi post v
            this.fbService.getAllPostInPageByID(this.fanpageSelected['access_token'], this.fanpageSelected['id']).then(result3 => {
              this.dataPagingPost = result3.paging
              let processPost: Array<any> = []
              //Bc 5: ly danh sch tng comment da trn mi bi post
              result3.data.forEach(item => processPost.push(this.fbService.getCommentByPostID(this.fanpageSelected['access_token'], item.id)))
              Promise.all(processPost).then((result4: Array<any>) => {
                let commentArray: Array<any> = []
                let detailCommentArray: Array<any> = []
                result4.forEach((item, index) => {
                  item.data.forEach(data => {
                    commentArray.push({
                      id_post: result3.data[index].id,
                      ...data
                    })
                    detailCommentArray.push(this.fbService.getCommentByID(this.fanpageSelected['access_token'], data.id))
                  })
                })
                //Bc 6 Ly chi tit comment
                Promise.all(detailCommentArray).then((result5: Array<any>) => {
                  this.listCommentPost = result5.map((item, index) => {
                    return { ...commentArray[index], ...item }
                  })
                  Promise.all(this.getListIDUser(this.listCommentPost)).then((result6: Array<any>) => {
                    this.listUserComment = result6
                    this.renderListUserIntoPost(result6)
                    this.loading = false
                  }).catch(() => { this.messageError(); this.loading = false })

                }).catch(() => { this.messageError(); this.loading = false })

              }).catch(() => { this.messageError(); this.loading = false })
            })
          }).catch(() => { this.messageError(); this.loading = false })
        }).catch(() => { this.messageError(); this.loading = false })
      }).catch(() => { this.messageError(); this.loading = false })
    }).catch(() => { this.messageError(); this.loading = false })
  }
  /**
   * Thay i page qun l
   */
  changePage(page) {
    this.fanpageSelected = page
    this.changeFanpageSelected.emit(this.fanpageSelected)
    this.clearPageSelected()
    this.getData()
  }
  /**
   * Xa page ny ra khi list chn, v page ny l page hin hnh
   */
  clearPageSelected() {
    let listTemp = [...this.listSubFanpage]
    listTemp.splice(listTemp.findIndex(item => item.id == this.fanpageSelected.id), 1)
    this.listFanpage = [...listTemp]
  }
  logout() {
    this.fbService.setStatusLogin(false)
    this.router.navigate(['/sales/dashboard/manage/socials-channel/facebook'])
  }
  viewMessageDetail(conversation) {
    this.changeInfoSender.emit(conversation.participants?.data[0])
    this.router.navigate(['/sales/dashboard/manage/socials-channel/facebook/conversation'],
      { queryParams: { conversationId: conversation.id } }
    )
  }
  viewCommentDetail(comment) {
    let dataUser = {
      id: comment.participants?.data[0].id,
      name: comment.participants?.data[0].name,
      profile_pic: comment.participants?.data[0].picture?.data?.url
    }
    this.changeInfoSender.emit(dataUser)
    this.router.navigate(['/sales/dashboard/manage/socials-channel/facebook/conversation'],
      { queryParams: { commentId: comment.id } }
    )
  }
  searchUser(value: string) {
    let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
    if (this.selectedIndex == 0)
      this.listConversation = this.vhAlgorithm.searchList(val, this.listSubConversation, ['senderName'])
    else this.listCommentPost = this.vhAlgorithm.searchList(val, this.listSubCommentPost, ['senderName'])
  }
  /**
   * Hm ny tr v danh sch nhng id_user  comment
   * @param listComment 
   */
  getListIDUser(listComment: Array<any>) {
    let arrayID = []
    let listArrayReturn = []
    listComment.forEach(item => {
      if (item.from && this.fanpageSelected.id != item.from.id && !arrayID.includes(item.from.id)) {
        arrayID.push(item.from.id)
        listArrayReturn.push(this.fbService.getInfoUser(item.from.id, this.fanpageSelected['access_token']))
      }
    })
    return listArrayReturn
  }


  messageError() {
    this.vhComponent.alertMessageDesktop('error', 'Xy ra li trong li qu trnh ng b')
  }
  renderListUserIntoPost(listUser: Array<any>) {
    this.listCommentPost.forEach(item => {
      if (item.from && item.from.id == this.fanpageSelected.id) {
        item.senderName = this.fanpageSelected.name
        item.participants = { data: [] }
        item.participants.data[0] = {
          id: this.fanpageSelected.id,
          name: this.fanpageSelected.name,
          picture: {
            data: { url: this.fanpageSelected.image_url }
          }
        }
      } else if (item.from && item.from.id != this.fanpageSelected.id) {
        let user = listUser.find(userInfo => userInfo.id == item.from.id)
        item.participants = { data: [] }
        item.participants.data[0] = user
        item.senderName = user.name
      }
    })
    this.listCommentPost = this.vhAlgorithm.sortDatebyDESC(this.listCommentPost, 'created_time')
    this.listSubCommentPost = [...this.listCommentPost]
  }
  trackByFnChat(index, item) {
    return index;
  }
  getNewConversation(data) {
    //ly v danh sch hi thoi sau  ra cuc hi thoi u tin
    this.fbService.getListConversationID(this.fanpageSelected['access_token'], this.fanpageSelected['id']).then((result1) => {
      if (!result1.error) {
        let newConversation = result1.data[0]
        //ly danh sch tin nhn ca cuc hi thoi ny v
        this.fbService.getListMessageByConversationID(this.fanpageSelected['access_token'], newConversation.id).then((result2: any) => {
          if (!result2.error) {
            newConversation = result2
            //ly thng tin ngi nhn v
            this.fbService.getInfoUserByPSID(result2.participants.data[0].id, this.fanpageSelected['access_token']).then((result3: any) => {
              if (!result3.error)
                newConversation.participants.data[0].profile_pic = result3.profile_pic
              else newConversation.participants.data[0].profile_pic = 'assets/icon/no-avatar.svg'
              newConversation.senderName = newConversation.participants.data[0].name
              newConversation.messages.data[0] = { ...newConversation.messages.data[0], ...data, message: data.message.text || '' }
              this.listConversation.unshift(newConversation)
            })
          }
        })
      }
    })
  }
  //hm ny  render d liu khp vi d liu trong listComment hin ti
  renderDataNewComment(data, user) {
    return {
      id_post: data.id_post, created_time: data.created_time, from: data.from,
      message: data.message, id: data.comment_id, can_comment: true, can_remove: true, can_hide: true,
      can_like: true, can_reply_privately: true, comment_count: 0, like_count: 0, user_likes: false, is_hidden: false,
      participants: { data: [user] }, senderName: user.name
    }
  }
  /**Ti thm tin nhn */
  loadMoreMessage() {
    this.loadingMoreMessage = true;
    this.fbService.getNextPaging(this.dataPagingMess.next).then(res => {
      if (res.error) {
        this.messageError()
        this.loadingMoreMessage = false
      } else {
        this.dataPagingMess = res.paging
        let listConversation = res.data;
        let conversation: Array<any> = []
        listConversation.forEach(item => {
          //bc 2 i vi mi cuc hi thoi phi get danh sch nhn tin
          conversation.push(this.fbService.getListMessageByConversationID(this.fanpageSelected['access_token'], item.id))
        })
        Promise.all(conversation).then((result: Array<any>) => {
          listConversation = result
          // //bc 3 i vi mi tn phi ly ra tn u tin  hin th v get thng tin ngi gi
          let messages: Array<any> = []
          listConversation.forEach((item: any) => {
            messages.push(this.fbService.getInfoUserByPSID(item.participants.data[0].id, this.fanpageSelected['access_token']))
          })
          Promise.all(messages).then((results: any) => {
            for (let index in results) {
              if (!results[index].error)
                listConversation[index].participants.data[0].profile_pic = results[index].profile_pic
              else listConversation[index].participants.data[0].profile_pic = 'assets/icon/no-avatar.svg'
              listConversation.senderName = listConversation[index].participants.data[0].name
            }
            let firstMessage: Array<any> = []
            listConversation.forEach((item: any) => {
              firstMessage.push(this.fbService.getMessageDetailByMessageID(this.fanpageSelected['access_token'], item.messages?.data[0]?.id))
            })
            Promise.all(firstMessage).then((results2: any) => {
              for (let index in results2) {
                if (!results2[index].error)
                  listConversation[index].messages.data[0] = { ...listConversation[index].messages.data[0], ...results2[index] }
              }
              this.listConversation = this.listConversation.concat(listConversation)
              this.listSubConversation = [...this.listConversation]
              this.loadingMoreMessage = false
            })
          })
        })
      }
    })
  }
  displayingSenderAction(item) {
    if (item.unread_count >= 0) {
      this.fbService.displayingSenderAction(this.fanpageSelected['access_token'], item.participants?.data[0].id, 'typing_on').then(res => item.unread_count = 0)
    }

  }
}
