import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-delivery',
  templateUrl: './delivery.component.html',
  styleUrls: ['./delivery.component.scss']
})
export class DeliveryComponent {
  @Input("id_delivery") id_delivery;
  @Output("closeAndReceiveDelivery") closeAndReceiveDelivery = new EventEmitter<any>();
  public deliveryList: any[] = this.vhQuerySales.getlocalDeliverys().filter(item => item.active);
  constructor(
    private vhQuerySales: VhQuerySales,
    private languageService: LanguageService
  ) {
    this.deliveryList.unshift({
      name: this.languageService.translate("None"),
      _id: '',
      type: 0
    })
    if (this.id_delivery) this.id_delivery = this.deliveryList[0]._id;
  }

  public selectDelivery(item?) {
    if (item) {
      this.closeAndReceiveDelivery.emit(item)
    } this.closeAndReceiveDelivery.emit(false)
  }

}
