import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { VhQuerySales } from 'ionic-vhframeworks';
import { FacebookService } from '../../services/facebook.service';

@Component({
  selector: 'app-facebook-conversation',
  templateUrl: './facebook-conversation.component.html',
  styleUrls: ['./facebook-conversation.component.scss']
})
export class FacebookConversationComponent implements OnInit {
  listFanpage: Array<any> = []
  fanpageSelected: any = {}
  infoSender: any = {} //thng tin ngi gi tin nhn
  typeDialog: 1 | 2 = 1 //1 l message , 2 l comment
  newMessage: any = {}
  newComment: any = {}
  pushMessage: any = {}
  list_earning_point_bills  = []
  list_earning_points_products  = []
  customer_group = []
  constructor(
    private fbService: FacebookService,
    private routes: ActivatedRoute,
    private vhQuerySales: VhQuerySales
  ) { this.subscribeParams() }

  ngOnInit(): void {
    this.listFanpage = this.fbService.getListAccountConnect().listPage
    this.fanpageSelected = this.listFanpage[0]
    //lng nghe message
   this.subscribePage(this.fanpageSelected.id)
   this.getSaleProgram()
  }

  getSaleProgram(){
    Promise.all([
      this.vhQuerySales.getEarningPointsBills(),
      this.vhQuerySales.getEarningPointsProducts(),

    ])
      .then(([earning_point_bills, earning_points_products,]: any) => { 
        this.list_earning_point_bills = earning_point_bills
        this.list_earning_points_products =   earning_points_products
        this.customer_group = this.vhQuerySales.getlocalCustomerClasss()
        
      })
  }


  ngOnDestroy() {
    this.unscriblePage()
  }
  subscribePage(id_page: string) {
    //lng nghe message
    this.fbService.connectWebhook(id_page, this.fanpageSelected.access_token)
    this.vhQuerySales.observableWebhookMessagesFacebook(id_page).subscribe((entry: any) => {
      this.newMessage = entry.messaging
    })
    //lng nghe comment
    this.vhQuerySales.observableWebhookFeedFacebook(id_page).subscribe((entry: any) => {
      if (entry.changes && this.fanpageSelected.id == entry.id) this.newComment = entry.changes[0]
    })
  }
  subscribeParams() {
    this.routes.queryParams.subscribe(val => {
      if (val.conversationId) this.typeDialog = 1
      else if (val.commentId) this.typeDialog = 2
    });
  }

  changeFanpageSelected(page) {
    this.unscriblePage()
    if (page) this.fanpageSelected = page
    this.subscribePage(this.fanpageSelected.id)
  }
  changeInfoSender(info) {
    info && (this.infoSender = info)
  }
  emitNewMessageFB(event) {
    if (event) {
      event.from.name = this.infoSender.name
      event.from.profile_pic = this.infoSender.profile_pic
      this.pushMessage = event
    }
  }
  unscriblePage() {
    this.vhQuerySales.unsubscribeWebhookMessagesFacebook(this.fanpageSelected.id)
    this.vhQuerySales.unsubscribeWebhookFeedFacebook(this.fanpageSelected.id)

  }

  test(event){
    
    this.infoSender =  {...event, ...this.infoSender};
  }
  
}
