import { Component, OnInit } from '@angular/core';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhSaleInvoiceDetail, VhSaleInvoices } from 'src/app/sales/interface/vh-sale-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-fb-create-order',
  templateUrl: './fb-create-order.component.html',
  styleUrls: ['./fb-create-order.component.scss']
})
export class FbCreateOrderComponent implements OnInit {
  searchProductList: boolean = false
  default_price_type: number = 1
  invoice: VhSaleInvoices = new VhSaleInvoices()
  employee: any = {}
  promotions: Array<any> = []
  listTax: Array<any> = []
  display_promotion_selling_price: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  constructor(
    public vhAuth: VhAuth,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    public vhAlgorithm: VhAlgorithm
  ) { }

  ngOnInit(): void {
    // set nhn vin mc nh
    if (!this.invoice.getIdEmployee())
      this.invoice.setIdEmployee(this.vhAuth.getUser()._id)
    this.employee = this.vhAuth.getlocalEmployee(this.invoice.getIdEmployee());
    this.invoice.setIdBranch(this.vhQuerySales.getDefaultBranch()._id)
    if (this.vhAuth.checkMyPermission('default_price_type_1')) this.default_price_type = 1;
    else if (this.vhAuth.checkMyPermission('default_price_type_2')) this.default_price_type = 2;
    this.invoice.setPriceType(this.default_price_type)
    // this.invoice.setPriceType(this.default_price_type)
    
  }
  /**HM KIM TRA KHUYN MI */
  private checkPromotions(product) {
    return new Promise<any>(resolve => {
      for (let i = 0; i < this.promotions.length; i++) {
        let promotion = this.promotions[i].promotion_detail.find(item => item.id_product == product._id)
        if (promotion) {
          this.vhComponent.showToast(5000,
            `${product.name} ${this.lang.translate("is getting a")} ${this.promotions[i].promotion.type == 1 ? this.vhAlgorithm.vhcurrencyunit_symbol(promotion.discount) : this.vhAlgorithm.vhpercent(promotion.discount / 100)} ${this.lang.translate("discount from")} ${this.promotions[i].promotion.name}`, "success-toast")
          resolve(promotion);
          break;
        }
      }
      resolve(null)
    })
  }
  closeSearch(data) {
    if (data) {
      this.checkPromotions(data).then(promotion => {
        let price = this.default_price_type == 1 ? data.price : data.price2 ? data.price2 : data.price // check 2 k l kiu gi bn v c gi bn s hay ko
        this.invoice.addInvoiceDetail({ ...data, price: price }, promotion ? promotion.new_price : null)
      })
    }
    this.searchProductList = false;
  }

  /**
  *  s kin nhn thay i gi t output component app-change-price-type
  * @event : boolean | null
  */
  changePriceType(event) {
    if (event != null) { // nu c thay i kiu gi bn th cp nht li gi cc sn phm thnh gi bn s
      this.default_price_type = event;
      if (this.invoice.getInvoiceDetail().length) { // nu c  length mi update
        let bill_details = this.invoice.getInvoiceDetail()
        for (let i in bill_details) {
          let price_update = 0;
          let product = this.vhQuerySales.getlocalDetailProduct(bill_details[i].id_subproduct ? bill_details[i].id_subproduct : bill_details[i].id_product);
          product = { ...product, ...this.vhQuerySales.getUnit_byRatio(product.units, bill_details[i].ratio) }
          if (event == 1) {
            price_update = product.price
          }
          else {
            price_update = product.price2 ? product.price2 : product.price
          }
          bill_details[i].price = price_update;
          bill_details[i].price_origin = price_update;
        }
        this.invoice.setSubTotal();
      }
      this.invoice.setPriceType(event)
    }
  }
  deleteGoods(detail: VhSaleInvoiceDetail) {
    this.invoice.removeInvoiceDetail(detail.id_product)
  }

  checkQuantityProduct(product: VhSaleInvoiceDetail, event) {
    if (!event) product.quantity = 1;
    this.invoice.setSubTotal();
  }
  // -------modal edit unit price-------
  isVisibleEditUnitPrice: boolean = false;
  unit_price: any;
  tempDetail: VhSaleInvoiceDetail;
  openEditUnitPrice(detail: VhSaleInvoiceDetail) {
    this.tempDetail = detail;
    this.isVisibleEditUnitPrice = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.unit_price = this.vhAlgorithm.vhnumeral('.unit_price');
    });
  }
  handleEditUnit() {
    if (this.unit_price.getRawValue() != '' && this.unit_price.getRawValue() >= 0) {
      this.tempDetail.price = parseFloat(this.unit_price.getRawValue());
      this.invoice.setSubTotal();
      this.isVisibleEditUnitPrice = false;
    } else { this.vhComponent.showToast(5000, this.lang.translate("Invalid data, please check again"), "alert-toast") }
  }
  handleCancelEditUnit() { this.isVisibleEditUnitPrice = false }
  // -------set employee-------
  closeAndReceiveEmployee(event) {
    if (event) {
      this.invoice.setIdEmployee(event);
    }
  }
  // ----set customer-----
  radioValue: 1 | 2 = 1;
  closeAndReceiveCustomer(event) {
    if (event) {
      this.invoice.setIdPartner(event);
      if (!this.vhQuerySales.getlocalCustomer(event).debt_enable) this.invoice.setPayment(this.invoice.getTotalATax());
    }
  }
  changeCustomerType(event) {
    if (this.radioValue == 1) {
      this.invoice.setIdPartner("id_retail");
      this.invoice.setPayment(this.invoice.getTotalATax());

    }
  }
  // ----set delivery ------
  closeAndReceiveDelivery(event) {
    if (event) {
      this.invoice.setIdDelivery(event);
    }
  }
  closeAndGetValuePayment(event) {
    if (event[0] == 1 || event[0] == 2) {
      this.setPaymentType(event[0]);
    }
    else if (event[0] == 3) {
      this.setPaymentType(event[0], event[1]);
    }
  }
  setPaymentType(type: 1 | 2 | 3, id_wallet?) {
    this.invoice.setPaymentType(type)
    switch (type) {
      case 1: {
        this.invoice.setIdWallet('id_cash'); this.invoice.setPayment(this.invoice.getPayment() ? this.invoice.getPayment() : null);

        break;
      }
      case 2: {
        this.invoice.setPayment(0); this.invoice.setIdWallet(null);
        this.invoice.setCash(0);

        break;
      }
      case 3: {
        if (id_wallet) {
          this.invoice.setIdWallet(id_wallet)
          this.invoice.setPayment(this.invoice.getPayment() ? this.invoice.getPayment() : null);

          break;
        }
      }
    };
  }
  setPickerTime(event) {
    this.invoice.setDate(event);
  }
  // -------change tax------
  id_tax: string;
  changeTax(event) {
    let tax = this.listTax.find(item => item._id == event);
    this.invoice.setTax(tax.value);
  }
  discount_price: any;
  editDiscount() {
    if (this.discount_price.getRawValue() && this.discount_price.getRawValue() >= 0) {
      this.invoice.setDiscount(parseFloat(this.discount_price.getRawValue()));
    } else {
      document.getElementById("discount-price")['value'] = this.invoice.getDiscount();
      this.discount_price = this.vhAlgorithm.vhnumeral('.discount-price');
    }
  }
  invoice_fee: any;
  editFee() {
    if (this.invoice_fee.getRawValue() && this.invoice_fee.getRawValue() >= 0) {
      this.invoice.setFee(parseFloat(this.invoice_fee.getRawValue()));
    } else {
      document.getElementById("invoice-fee")['value'] = this.invoice.getFee();
      this.invoice_fee = this.vhAlgorithm.vhnumeral('.invoice-fee');
    }
  }
  // -------modal edit pay---------
  edit_pay: boolean = false;
  edit_paytotal: any;
  showModalEditTotalPay() {
    if (this.invoice.getPaymentType() != 2 && this.vhQuerySales.getlocalCustomer(this.invoice.getIdPartner()).debt_enable) {
      this.vhAlgorithm.waitingStack().then(() => {
        this.edit_paytotal = this.vhAlgorithm.vhnumeral(".edit-pay");
      })
      this.edit_pay = true;
    }
    else if (!this.vhQuerySales.getlocalCustomer(this.invoice.getIdPartner()).debt_enable) {
      this.vhComponent.showToast(5000,
        this.lang.translate("This customer is not allowed to debt"), "alert-toast"
      )
    }
  }
  handleEditPay() {
    if (this.edit_paytotal.getRawValue() >= 0 && this.edit_paytotal.getRawValue() <= this.invoice.getTotalATax()) {
      this.invoice.setPayment(parseFloat(this.edit_paytotal.getRawValue()))
    }
    this.edit_pay = false;
  }
  handleCancelEditPay() {
    this.edit_pay = false;
  }
  payInvoice(bill_type, print) {

  }
}
