import { DatePipe } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { VhAlgorithm } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { filter } from 'rxjs/operators';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FacebookService } from '../../../services/facebook.service';

@Component({
  selector: 'app-conversation-comment',
  templateUrl: './conversation-comment.component.html',
  styleUrls: ['./conversation-comment.component.scss']
})
export class ConversationCommentComponent implements OnInit {

  commentId: string = ''
  loading: boolean = false
  @Input() fanpageSelected: any = {}
  @Input() infoSender: any = {}
  messageList: Array<any> = []
  dataPaging: any = {}
  messageText: string = ''
  loadingMoreMessage: boolean = false
  listAttachment: Array<any> = []
  loadingAttachment: boolean = false
  searchProductList: boolean = false
  productSelected: any = {}
  expiresToSend: boolean = false
  post: any = {}
  comment: any = {}
  childComment: Array<any> = []
  listUserJoinComment: Array<any> = []
  infoSendMessage: any = {}
  constructor(
    private fbService: FacebookService,
    private vhComponent: VhComponent,
    private routes: ActivatedRoute,
    public vhAlgorithm: VhAlgorithm,
    private datePiPe: DatePipe,
    private modal: NzModalService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.subscribeRouter()
  }
  //subscribe router
  subscribeRouter() {
    this.routes.queryParams.pipe(filter(params => params.commentId))
      .subscribe(val => {
        this.commentId = val.commentId
        this.commentId != '' && this.getData()
      }
      );
  }
  /**
   * Ly bi post v ly chi tit tin nhn hin th ra
   */
  getData() {
    this.loading = true
    this.dataPaging = {}
    this.childComment = []
    Promise.all([
      this.fbService.getPostByIdPost(this.fanpageSelected['access_token'], this.fanpageSelected['id'], this.commentId.split('_')[0]),
      this.fbService.getCommentByID(this.fanpageSelected['access_token'], this.commentId),
      this.fbService.getReplyCommentByID(this.fanpageSelected['access_token'], this.commentId)
    ]).then(([post, comment, childComment]) => {
      this.post = post
      this.comment = comment
      this.dataPaging = childComment.paging
      //Tip tc ly chi tit comment theo id
      if (childComment.data.length > 0) {
        this.getPromiseDetailComment(childComment.data).then((result1: Array<any>) => {
          this.getListIDUser(result1).then((result2: Array<any>) => {
            this.childComment = this.renderListUserIntoPost(result1, result2)
            this.loading = false
          })
        })
      } else this.loading = false
    }).catch(() => { this.showMessageError(); this.loading = false })
  }

  /**
   * Hm ny tr v promise detail comment
   * @param data 
   */
  getPromiseDetailComment(data: Array<any>): Promise<any> {
    let process: Array<any> = []
    data.forEach(item => { process.push(this.fbService.getCommentByID(this.fanpageSelected['access_token'], item.id)) })
    return Promise.all(process)
  }
  /**
   * Hm ny tr v danh sch nhng id_user  comment
   * @param listComment 
   */
  getListIDUser(listComment: Array<any>) {
    let arrayID = []
    let listArrayReturn = []
    listComment.forEach(item => {
      if (item.from && this.fanpageSelected.id != item.from.id && !arrayID.includes(item.from.id)) {
        arrayID.push(item.from.id)
        listArrayReturn.push(this.fbService.getInfoUser(item.from.id, this.fanpageSelected['access_token']))
      }
    })
    return Promise.all(listArrayReturn)
  }
  /**
   * Ly message sticker
   * @param messageList 
   * @returns 
   */
  getStickerMessage(messageList: Array<any>): Promise<any> {
    return new Promise((resolve, reject) => {
      let shares: Array<any> = []
      messageList.forEach(item => {
        if (item.shares) shares.push(this.fbService.getLinkShares(this.fanpageSelected['access_token'], item.id))
      })
      Promise.all(shares).then((res: any) => { resolve(res) }).catch(() => reject())
    })
  }

  ngAfterViewChecked() {
    if (document.getElementById('conversation-dialog') && document.getElementById('conversation-dialog-message')) {
      let height = document.getElementById('conversation-dialog').clientHeight - (this.listAttachment.length > 0 ? 220 : 160)
      document.getElementById('conversation-dialog-message').style.height = `${height}px`
    }
  }
  public renderDate(date) {
    return this.datePiPe.transform(date, 'dd/MM/YYYY HH:mm')
  }
  trackByFn(index, item) {
    return index;
  }
  /**
   * S kin typing bt k phm no, bt s kin enter  send message
   * @param event 
   */
  keyPress(event: KeyboardEvent) {
    const keyCode = event.which || event.keyCode;
    if (keyCode == 13 && !event.shiftKey) {
      event.preventDefault()
      this.sendAllMessage()
    }
  }
  sendAllMessage() {
    let dataPush: any = {
      id: '', can_comment: true, can_hide: true, can_like: true, can_remove: true, can_reply_privately: true,
      comment_count: 0, created_time: new Date(), from: { name: this.fanpageSelected.name, id: this.fanpageSelected.id },
      is_hidden: false, like_count: 0, message: this.messageText, user_likes: false, senderName: this.fanpageSelected.name,
      participants: { data: [{ picture: { data: { url: this.fanpageSelected.image_url } } }] }
    }
    if (this.messageText && (!this.listAttachment.length || !this.listAttachment[0].loadingAttachment)) {
      let json: any = { message: `${this.messageText}` }
      this.childComment.unshift(dataPush)
      this.messageText = ''
      this.fbService.replyCommentFacebookText(this.fanpageSelected.access_token, this.commentId, json).then(res => {
        if (res.error) {
          this.vhComponent.alertMessageDesktop('error', res.error?.message);
          this.childComment.splice(0, 1)
        } else this.childComment[0].id = res.id
        if (this.listAttachment.length) {
          dataPush.message = ''
          this.childComment.unshift(dataPush)
          this.sendMessageImage()
        }
      })
    } else if (this.listAttachment.length && this.listAttachment[0].loadingAttachment)
      this.vhComponent.alertMessageDesktop('warning', 'ang ti nh ln, vui lng ch giy lt...')
    else if (this.listAttachment.length && !this.listAttachment[0].loadingAttachment) {
      this.childComment.unshift(dataPush)
      this.sendMessageImage()
    }
  }
  /**
   * Gi hnh nh
   */
  sendMessageImage() {
    this.childComment[0] = {
      attachment: {
        media: { image: { src: this.listAttachment[0].buffer } }, type: 'photo'
      },
      ...this.childComment[0]
    }
    this.fbService.replyCommentFacebookImage(this.commentId, this.listAttachment[0].formData).then(res => {
      if (res.error) {
        this.vhComponent.alertMessageDesktop('error', res.error?.message);
        this.childComment.splice(0, 1)
      } else {
        this.childComment[0].id = res.id
      }
    })
    this.listAttachment = []
  }

  appendEmoji(emoji: string) {
    this.messageText += emoji
  }
  loadMoreMessage() {
    this.loadingMoreMessage = true;
    this.fbService.getNextPaging(this.dataPaging.next).then(childComment => {
      if (childComment.error) {
        this.showMessageError()
        this.loadingMoreMessage = false
      } else {
        this.getPromiseDetailComment(childComment.data).then((result1: Array<any>) => {
          this.getListIDUser(result1).then((result2: Array<any>) => {
            this.childComment = this.childComment.concat(this.renderListUserIntoPost(result1, result2))
            this.dataPaging = childComment.paging
            this.loadingMoreMessage = false
          })
        })
      }
    }).catch(() => { this.showMessageError(); this.loadingMoreMessage = false })
  }
  /**
   * Upload hnh nh
   * @param event 
   */
  onUploadImage(event: any) {
    const fileReader = new FileReader();
    const readerBase64 = new FileReader();
    const file = event.target.files[0];
    readerBase64.readAsDataURL(file)
    readerBase64.onload = (e: any) => {
      fileReader.onloadend = async () => {
        const photoData = new Blob([fileReader.result], { type: 'image/jpg' });
        const formData = new FormData();
        formData.append('access_token', this.fanpageSelected.access_token);
        formData.append('source', photoData);
        this.listAttachment.push({ formData, buffer: readerBase64.result, loadingAttachment: false })
      };
      fileReader.readAsArrayBuffer(file);
    }
  }
  updateImage() {
    if (this.listAttachment.length >= 1)
      this.vhComponent.alertMessageDesktop('warning', 'Ch c gi ti a 1 nh. Vui lng th li')
    else document.getElementById('image-message').click()
  }

  removeImage(image: any, index: number) {
    this.listAttachment.splice(index, 1)
  }
  showMessageError() {
    this.vhComponent.alertMessageDesktop('error', "Xy ra li khi ng b, vui lng th li")
  }
  isVisibleSendMessage: boolean = false
  closeSearch(data) {
    if (data) {
      this.productSelected = data
      this.isVisibleSendMessage = true
    }
    this.searchProductList = false;
  }
  sendProductAndPrice() {
    this.messageText = `Tn sn phm: ${this.productSelected['name']}, Gi : ${this.vhAlgorithm.vhcurrencyunit_symbol(this.productSelected['price'])}`
    this.isVisibleSendMessage = false
  }
  sendMessageUrl(all?) {
    this.messageList.push({
      id: '',
      from: this.fanpageSelected,
      to: { data: [this.infoSender] },
      message: '',
      created_time: new Date().toISOString(),
      attachments: {
        data: [{ image_data: { preview_url: this.productSelected['img'], url: this.productSelected['img'] } }]
      }
    })
    this.isVisibleSendMessage = false
    this.fbService.sendImageWithUrl(this.fanpageSelected['access_token'], this.productSelected['img'], this.infoSender['id']).then(res => {
      if (res.error) {
        this.vhComponent.alertMessageDesktop('error', "Xy ra khi gi hnh nh, vui lng th li")
        this.messageList.splice(this.messageList.length - 1, 1)
      } else this.messageList[this.messageList.length - 1].id = res['message_id']

      if (all) {
        this.sendProductAndPrice()
        this.sendAllMessage()
      }
    })
  }

  gotoInbox() {
    window.open(`https://www.facebook.com/${this.fanpageSelected.id}/inbox`, '_blank')
  }
  likeCommentByCommentId(comment) {
    this.fbService.likeCommentByCommentId(this.fanpageSelected['access_token'], comment.id, comment.user_likes)
    comment.user_likes = !comment.user_likes
  }
  /**
   * Hm nm  render thng tin ngi dng  bnh lun
   * @param listUser 
   */
  renderListUserIntoPost(childComment: Array<any>, listUser: Array<any>) {
    childComment.forEach(item => {
      if (item.from && item.from.id == this.fanpageSelected.id) {
        item.senderName = this.fanpageSelected.name
        item.participants = { data: [] }
        item.participants.data[0] = {
          id: this.fanpageSelected.id,
          name: this.fanpageSelected.name,
          picture: {
            data: { url: this.fanpageSelected.image_url }
          }
        }
      } else if (item.from && item.from.id != this.fanpageSelected.id) {
        let user = listUser.find(userInfo => userInfo.id == item.from.id)
        item.participants = { data: [] }
        item.participants.data[0] = user
        item.senderName = user.name
      }
    })
    return this.vhAlgorithm.sortDatebyDESC(childComment, 'created_time')
  }
  trackByMyComment(index, item) {
    return index;
  }
  deleteCommentByID(comment, emit?) {
    this.modal.confirm({
      nzTitle: 'Xc nhn',
      nzContent: '<i>Bn c mun xa bnh lun ny?</i>',
      nzOkText: 'OK',
      nzOnOk: () => {
        return new Promise((resolve, reject) => {
          this.fbService.deleteCommentByID(this.fanpageSelected['access_token'], comment.id).then(res => {
            if (res.success) {
              comment.deleted = true
              emit && this.router.navigate(['/sales/dashboard/manage/socials-channel/facebook/conversation'], { queryParams: { deleteCommentID: comment.id } })
            }
            else this.vhComponent.alertMessageDesktop('error', 'Xa bnh lun tht bi')
            resolve(true)
          })
        })
      },
      nzOnCancel: () => { },
      nzCancelText: 'Hy'
    })
  }
  hideCommentByID(comment) {
    this.modal.confirm({
      nzTitle: 'Xc nhn',
      nzContent: '<i>Bn c mun n bnh lun ny?</i>',
      nzOkText: 'OK',
      nzOnOk: () => {
        return new Promise((resolve, reject) => {
          this.fbService.updateCommentByID(this.fanpageSelected['access_token'], comment.id, { is_hidden: !comment.is_hidden }).then(res => {
            if (res.success)
              comment.is_hidden = !comment.is_hidden
            else this.vhComponent.alertMessageDesktop('error', 'n bnh lun tht bi')
            resolve(true)
          })
        })
      },
      nzOnCancel: () => { },
      nzCancelText: 'Hy'
    })
  }

  sendPrivateMessage() {
    let json = {
      recipient: { comment_id: this.infoSendMessage.id },
      message: { text: this.infoSendMessage.textMessage }
    }
    this.fbService.replyPrivateMessageOnComment(this.fanpageSelected.access_token, json).then(res => {
      if (res.comment_id)
        this.vhComponent.alertMessageDesktop('success', 'Phn hi bnh lun thnh cng')
      else this.vhComponent.alertMessageDesktop('error', 'Bnh lun ny  c phn hi trc , vui lng kim tra li')
      this.infoSendMessage = {}

    })
  }
}
